<template>
     <b-form @submit.prevent="createProduct">
      <b-form-group label="Name:" label-for="name">
        <b-form-input id="name" v-model="productData.Name" :disabled="user_type !== 'Engineer'" required></b-form-input>
      </b-form-group>

      <b-form-group label="Code:" label-for="code">
        <b-form-input id="code" v-model="productData.Code" :disabled="user_type !== 'Engineer'" required></b-form-input>
      </b-form-group>

      <b-form-group label="Density:" label-for="density">
        <b-form-input id="density" v-model="productData.density" :disabled="user_type !== 'Engineer'"  required></b-form-input>
      </b-form-group>
      <br />
      <b-button type="submit" variant="outline-secondary" :disabled="user_type !== 'Engineer'">Create</b-button>
    <b-button class="ms-3" @click="Close" variant="outline-success">Cancel</b-button>

      <!-- <b-button type="submit" variant="secondary">Create</b-button> -->
    </b-form>
</template>


<script>
import axios from 'axios';
// import Multiselect from 'vue-multiselect';
// import vSelect from "vue-select";


export default {
    props: ['Close', 'refresh'],

  data() {
    return {
        productData: {
        Name: '',
        Code: '',
        density: '',
        Created_at: '',
      },
        products: [],
    };
  },
  created() {
    this.productData.Created_at = new Date().toISOString();
    // Fetch the list of tanks when the component is created
    this.fetchProducts();
    // Retrieve the data from localStorage
    const user = localStorage.getItem('user');
    // Parse the JSON string back to an object
    const parsedData = JSON.parse(user);
    this.user_type = parsedData.user_type;
  },
  methods: {
    alertError(msg) {
        this.$notify.error({
          title: 'Login Error',
          message: msg
        });
      },
    alertSuccess(msg) {
        this.$notify({
          title: 'Success',
          message: msg,
          type: 'success'
        });
      },
    closeAddTank() {
        this.CloseAdd_Tank();
    },
    createProduct() {
      // Your product creation logic
      axios.post('http://localhost:8000/products/', this.productData)
        .then(response => {
          console.log('Product created successfully:', response);
          this.alertSuccess("Product created successfully")
          // Refresh the list of products after creation
          this.Close()
          this.refresh();
        })
        .catch(error => {
          console.error('Error creating product:', error.response);
          this.alertError("Error creating product")
        });
    },
    fetchTanks() {
      // Fetch the list of tanks from the server
      axios.get('http://localhost:8000/tanks/')
        .then(response => {
          this.tanks = response.data;
        })
        .catch(error => {
          console.error('Error fetching tanks:', error.response);
        });
    },
    fetchProducts() {
    // Fetch the list of registered products from the server
    axios.get('http://localhost:8000/products/')
      .then(response => {
        this.products = response.data;
      })
      .catch(error => {
        console.error('Error fetching products:', error.response);
      });
  },
  editTank(tank) {
    // Implement your edit logic here
    console.log('Edit tank:', tank);
  },
  deleteTank(tank) {
    // Implement your delete logic here
    console.log('Delete tank:', tank);
  },
  },
};
</script>
