<script>
import axios from 'axios';


export default {
    props: ['editData', 'Close', 'refresh'],
  data() {
    return {
        tankData: {
            Name: '',
            product: '',
            Controller_polling_address: '',
            Tank_index: '',
            Capacity: '',
            Status: false,
            Tank_height: '',
        },
        tanks: [],
        products: [],
        selectedProductId: null,
        selectedShape: null,
        selectedUOM: null,
        selectedDUOM: null,
        measures: [
                    {
                    "name": "Liters",
                    "abbreviation": "L",
                    "id": 1
                },
                {
                    "name": "Tonnes",
                    "abbreviation": "T",
                    "id": 2
                },
                {
                    "name": "Kilogrammes",
                    "abbreviation": "kg",
                    "id": 3
                },
                {
                    "name": "Metres",
                    "abbreviation": "m",
                    "id": 4
                },
                {
                    "name": "Millimeters",
                    "abbreviation": "mm",
                    "id": 5
                },
                {
                    "name": "Centimeters",
                    "abbreviation": "cm",
                    "id": 6
                },
                {
                    "name": "Cubic Meters",
                    "abbreviation": "m³",
                    "id": 7
                },
                {
                    "name": "Gallons",
                    "abbreviation": "gal",
                    "id": 8
                },
                ],
        shapes: [
                {
                    "name": "Lying Cylindrical",
                    "id": 1
                },
                {
                    "name": "Standing Cylindrical",
                    "id": 2
                },
                {
                    "name": "Lying Rectangular",
                    "id": 3
                },
                {
                    "name": "Standing Rectangular",
                    "id": 4
                },
                {
                    "name": "Standing Round View",
                    "id": 5
                },
                ],

    };
  },
  created() {
        this.tankData.Created_at = new Date().toISOString();
        // Retrieve the data from localStorage
        const user = localStorage.getItem('user');
        // Parse the JSON string back to an object
        const parsedData = JSON.parse(user);
        this.user_type = parsedData.user_type;
        // Fetch the list of tanks when the component is created
        this.fetchProducts();
        this.selectedProductId = this.editData?.Product_id?.Name,
        this.tankData.Name=this.editData?.Name,
        this.tankData.product= this.editData?.Product_id,
        this.tankData.Controller_polling_address= this.editData?.Controller_polling_address,
        this.tankData.Tank_index= this.editData?.Tank_index,
        this.tankData.Capacity= this.editData?.Capacity,
        this.tankData.Status= this.editData?.Status,
        // this.tankData.Po4= this.editData?.Po4,
        // this.tankData.Display_unit= this.editData?.Display_unit,
        // this.tankData.Density= this.editData?.Density,
        this.tankData.Tank_height= this.editData?.Tank_height
  },
    methods: {
      alertError(msg) {
        this.$notify.error({
          title: 'Login Error',
          message: msg
        });
      },
    alertSuccess(msg) {
        this.$notify({
          title: 'Success',
          message: msg,
          type: 'success'
        });
      },
    closeEditTank() {
        this.Close();
    },
    updateTank() {
    // Your tank creation logic
    this.tankData['Product'] = this.selectedProductId.id
    this.tankData['Density'] = this.selectedProductId.density

    axios.put(`http://localhost:8000/tanks/${this.editData.Tank_id}`, this.tankData)
    .then(response => {
    console.log('Tank updated successfully:', response);
    this.alertSuccess('Tank updated successfully')
    this.refresh();
    this.Close();
    })
    .catch(error => {
        console.error('Error creating tank:', error.response);
        this.alertError("Error updating tank")
    });
    },
    fetchTanks() {
      // Fetch the list of tanks from the server
      axios.get('http://localhost:8000/tanks/')
        .then(response => {
          this.tanks = response.data;
        })
        .catch(error => {
          console.error('Error fetching tanks:', error.response);
        });
    },
    fetchProducts() {
    // Fetch the list of registered products from the server
    axios.get('http://localhost:8000/products/')
      .then(response => {
        this.products = response.data;
      })
      .catch(error => {
        console.error('Error fetching products:', error.response);
      });
  },
  
  },
};
</script>
